.header {
  height: 6.6rem;
  background-color: #fff;
  border-bottom: .4rem solid #40c057;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  padding: 0 4.8rem;
  display: grid;
  position: relative;
  box-shadow: 0 0 .3rem #0000003d;
}

#quizzer-img-container {
  width: auto;
  height: auto;
  place-self: center start;
  padding: 1.2rem;
  transition: all .2s;
}

.logo {
  height: 42px;
}

.logo:hover, .logo:active {
  transition: all .2s;
  transform: scale(1.1);
}

.main-nav {
  display: grid;
}

.main-nav-list {
  place-self: center end;
  gap: 2.4rem;
  list-style: none;
  display: flex;
}

.scale-up {
  transition: all .2s;
}

.main-nav-link {
  color: #fff;
  background-color: #40c057;
  border-radius: 10px;
  padding: .8rem;
  font-size: 1.8rem;
  font-weight: 450;
  text-decoration: none;
  transition: all .2s;
}

.sign-up {
  background-color: #ffb300;
}

.scale-up:hover, .scale-up:active {
  transition: all .2s;
  transform: scale(1.1);
}

